.frame-child {
  align-self: stretch;
  width: 331px;
  position: relative;
  border-radius: var(--br-xs-5);
  background-color: var(--color-lightgray);
  display: none;
  max-width: 100%;
}
.model-a1- {
  margin: 0;
  flex: 1;
  position: relative;
  font-size: inherit;
  font-weight: 900;
  font-family: inherit;
  white-space: nowrap;
  z-index: 1;
}
.frame-wrapper,
.rectangle-parent {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  box-sizing: border-box;
  max-width: 100%;
}
.rectangle-parent {
  flex: 1;
  border-radius: var(--br-xs-5);
  background-color: var(--color-lightgray);
  justify-content: flex-start;
  padding: 3px var(--padding-5xs) 0;
}
.frame-wrapper {
  align-self: stretch;
  justify-content: flex-end;
  padding: 0 32px 0 33px;
}
.frame-item,
.h1 {
  position: relative;
}
.h1 {
  margin: 0;
  font-size: inherit;
  font-weight: 900;
  font-family: inherit;
  white-space: nowrap;
}
.frame-item {
  height: 2px;
  width: 178px;
}
.vector-wrapper {
  align-self: stretch;
  flex-direction: row;
  align-items: flex-start;
  padding: 0 var(--padding-xl);
}
.powered-by,
.powered-by-8d,
.vector-wrapper {
  display: flex;
  justify-content: center;
}
.powered-by-8d {
  width: 137px;
  position: relative;
  font-weight: 900;
  align-items: center;
  white-space: nowrap;
}
.powered-by {
  align-self: stretch;
  flex-direction: row;
  align-items: flex-start;
  padding: 0 var(--padding-xl) 0 21px;
  font-size: 10px;
  color: var(--color-gray-200);
}
.frame-parent,
.parent {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-start;
}
.parent {
  gap: var(--gap-4xs);
  font-size: 32px;
}
.frame-parent {
  gap: 14px;
  max-width: 100%;
  text-align: center;
  font-size: var(--font-size-xl);
  color: var(--color-black);
  font-family: var(--font-gotham-pro);
}
.info-container-child {
  height: 134px;
  width: 386px;
  position: relative;
  border-radius: var(--br-3xs);
  background-color: var(--color-gainsboro);
  border: 2px solid var(--color-black);
  box-sizing: border-box;
  display: none;
  max-width: 100%;
}
.h11,
.separator-child {
  position: relative;
  z-index: 1;
}
.h11 {
  margin: 0;
  align-self: stretch;
  font-size: inherit;
  font-weight: 900;
  font-family: inherit;
  display: inline-block;
  min-width: 61px;
}
.separator-child {
  height: 1px;
  width: 31px;
  border-top: 1px solid var(--color-black);
  box-sizing: border-box;
}
.separator {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-end;
  padding: 0 15px;
}
.info-pair {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-start;
  gap: var(--gap-4xs);
}
.div,
.info-items {
  align-self: stretch;
}
.info-items {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 var(--padding-2xs);
}
.div {
  position: relative;
  font-size: var(--font-size-2xs);
  font-weight: 900;
  color: rgba(0, 0, 0, 0.26);
  z-index: 1;
}
.info-columns {
  width: 83px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 5px;
}
.divider-child {
  width: 0;
  flex: 1;
  position: relative;
  max-height: 100%;
  object-fit: contain;
  z-index: 1;
}
.divider {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-smi) var(--padding-2xs) 0 0;
}
.h2 {
  margin: 0;
  flex: 1;
  position: relative;
  font-size: inherit;
  font-weight: 900;
  font-family: inherit;
  display: inline-block;
  min-width: 96px;
  z-index: 1;
}
.subject-title {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-end;
  padding: 0 1px 0 0;
}
.no-server-connection {
  margin: 0;
}
.zero404-no-server-container1 {
  width: 100%;
}
.zero404-no-server-container {
  width: 94px;
  position: relative;
  font-size: var(--font-size-2xs);
  font-weight: 900;
  color: rgba(0, 0, 0, 0.56);
  display: flex;
  align-items: center;
  z-index: 1;
}
.subject-details {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-start;
  gap: 12px;
}
.info-rows,
.subject-container {
  flex: 1;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.subject-container {
  flex-direction: column;
  padding: 4px 0 0;
  font-size: var(--font-size-xl);
}
.info-rows {
  align-self: stretch;
  flex-direction: row;
  gap: 16px;
}
.powered-by-8d1 {
  flex: 1;
  position: relative;
  font-weight: 900;
  z-index: 1;
}
.footer {
  width: 85px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 10px;
  box-sizing: border-box;
  font-size: 3.4px;
  color: var(--color-gray-200);
}
.info-details,
.info-details-wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.info-details {
  align-self: stretch;
  height: 94px;
  justify-content: flex-start;
  gap: 8px;
}
.info-details-wrapper {
  flex: 1;
  justify-content: flex-end;
  padding: 0 0 6px;
  box-sizing: border-box;
  min-width: 145px;
}
.button-container-child {
  height: 108px;
  width: 103px;
  position: relative;
  border-radius: var(--br-5xs);
  background-color: var(--color-gray-100);
  display: none;
}
.div1 {
  flex: 1;
  position: relative;
  font-weight: 900;
  z-index: 2;
}
.button-container {
  width: 103px;
  border-radius: var(--br-5xs);
  background-color: var(--color-gray-100);
  flex-direction: row;
  align-items: flex-start;
  padding: 44px var(--padding-2xs) 49px 12px;
  min-width: 103px;
  z-index: 1;
  font-size: 13px;
  color: var(--color-white);
}
.button-container,
.content,
.info-container,
.iphone-14-15-pro-max-1 {
  display: flex;
  justify-content: flex-start;
  box-sizing: border-box;
}
.info-container {
  flex: 1;
  border-radius: var(--br-3xs);
  background-color: var(--color-gainsboro);
  border: 2px solid var(--color-black);
  flex-direction: row;
  align-items: flex-end;
  padding: var(--padding-5xs) var(--padding-4xs) var(--padding-5xs)
    var(--padding-smi);
  gap: 30px;
  max-width: 100%;
}
.content,
.iphone-14-15-pro-max-1 {
  align-items: flex-start;
}
.content {
  align-self: stretch;
  flex-direction: row;
  padding: 0 var(--padding-4xs) 0 5px;
  max-width: 100%;
  text-align: center;
  font-size: 24px;
  color: var(--color-black);
  font-family: var(--font-gotham-pro);
}
.iphone-14-15-pro-max-1 {
  width: 100%;
  position: relative;
  background-color: var(--color-white);
  overflow: hidden;
  flex-direction: column;
  padding: var(--padding-4xs) 17px 638px;
  gap: 48px;
  line-height: normal;
  letter-spacing: normal;
}
@media screen and (max-width: 350px) {
  .button-container {
    flex: 1;
  }
  .info-container {
    flex-wrap: wrap;
  }
}
